import React, { useState, useEffect } from "react";
import styles from "./Login.module.css";
import { useLang } from "../LangContext";
import { useTheme } from "../ThemeContext";
import * as elements from "./LoginPageElements";
import axios from "axios";
import { useAuth } from "../AuthContext";

function LoginPage({ windowSize, setCurPage }) {
  const [mailInput, setMailInput] = useState("");
  const [passInput, setPassInput] = useState("");
  const { setAuthData } = useAuth();

  const handleLogin = async () => {
    try {
      const response = await axios.post(
        "/api/login",
        {
          username: mailInput,
          password: passInput,
        },
        { withCredentials: true } // важное
      );

      // Сохраняем данные авторизации (промокоды, username, tickets и т.д.)
      setAuthData(response.data);
      console.log(response.data);

      // Перенаправляем на нужную страницу
      setCurPage("carousel");
    } catch (err) {
      console.error("Ошибка входа:", err);
      alert("Неверный логин или пароль");
    }
  };
  return (
    <div className={styles.wrapper}>
      <div className={styles.formWrapper}>
        <div className={styles.logoContainer}>{elements.logo}</div>
        <h6 className={styles.formHeader}>Вход</h6>
        <input
          type="text"
          value={mailInput}
          onChange={(e) => setMailInput(e.target.value)}
          className={styles.inputStyle}
          placeholder="e-mail"
          style={{ marginTop: "30px" }}
        />
        <input
          type="password"
          value={passInput}
          onChange={(e) => setPassInput(e.target.value)}
          className={styles.inputStyle}
          placeholder="пароль"
          style={{ marginTop: "14px" }}
        />
        <div className={styles.formButton} onClick={handleLogin}>
          войти
        </div>
        <span className={styles.noAccText}>нет аккаунта?</span>
        <a href="https://my.qwins.co" className={styles.signInRef}>
          зарегистрироваться
        </a>
        <div className={styles.footerContainer}>
          <p className={styles.footerHeader}>политика конфиденциальности</p>
          <p className={styles.footerHeader}>условия предоставления услуг</p>
        </div>
      </div>
      {!(windowSize.isXs || windowSize.isSm) && (
        <div className={styles.bannerWrapper}>
          <h1 className={styles.bannerHeader}>
            Современный облачный
            <br />
            хостинг провайдер
          </h1>
          <p className={styles.bannerFooterText}>
            Производительность и надежность гарантируют бесперебойную работу
            проектов с высокой доступностью.
            {(windowSize.isLg || windowSize.isXl) && <br />} Мы предлагаем
            выделенные ресурсы и установку любой ОС или ISO-образа.
          </p>
          {elements.bannerBg}
        </div>
      )}
    </div>
  );
}

export default LoginPage;
